.hero {
    width: 100%;
    height: 100vh;
    display: flex;
}

.hero span {
    color: rgba(0, 0, 0, 0.3);
}

.hero .left,
.hero .right {
    width: 48%;
    padding: 2%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero .right {
    align-items: center;
}

.hero .right img {
    width: 62%;
    height: 60%;
    border-radius: 70%;
    box-shadow: 0px 0px 38px 20px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(0, 0, 0, 0.3);
}

.randomSection {
    height: 100vh;
    display: flex;
}

.randomSection .right,
.randomSection .left {
    width: 48%;
    height: 100%;
    padding: 2%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    position: relative;
}

.randomSection .left .imgBorderTop {
    width: 200px;
    height: 200px;
    border-top: 10px solid rgba(0, 0, 0, 0.3);
    border-right: 10px solid rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 10%;
    right: 10%;
}

.randomSection .left .imgBorderBottom {
    width: 200px;
    height: 200px;
    border-top: 10px solid rgba(0, 0, 0, 0.3);
    border-right: 10px solid rgba(0, 0, 0, 0.3);
    position: absolute;
    bottom: 6%;
    left: 20%;
    transform: rotate(180deg);
}

.randomSection .right {
    padding-right: 100px;
}

.randomSection h1 {
    line-height: 50px;
}

.industries {
    width: 100%;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    justify-content: center;
}

.industries h1 {
    text-align: center;
    line-height: 50px;
}

.industries .stage {
    width: 90%;
    display: flex;
    flex-direction: column;
}

.industries .stage .item {
    width: 100%;
    display: flex;
    box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.3);
    border-radius: 25px;
    transition: all 0.2s ease;
    margin-bottom: 20px;
}

.industries .stage .item:hover {
    background: var(--black);
    color: white;
}

.industries .stage .item .left {
    width: 28%;
    padding: 2%;
}

.industries .stage .item .left img {
    width: 100%;
    height: 100%;
    border-radius: 25px;
}

.industries .stage .item .right {
    width: 68%;
    padding: 2%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
}