.hero {
    height: 100vh;
    display: flex;
}

.hero .left,
.hero .right {
    width: 48%;
    padding: 2%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    position: relative;
}

.hero .right img {
    z-index: 100;
}

.hero .right .circle {
    width: 400px;
    height: 400px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    position: absolute;
    right: 18%;
    bottom: 20%;
    z-index: 90;
}

.hero .left span {
    color: rgba(0, 0, 0, 0.3);
}

.hero .left h1,
.whyChooseUs .right h1 {
    line-height: 50px;
    text-transform: uppercase;
}

.whyChooseUs {
    height: 100vh;
    display: flex;
}

.whyChooseUs .right,
.whyChooseUs .left {
    width: 48%;
    height: 100%;
    padding: 2%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    position: relative;
}

.whyChooseUs .left .imgBorderTop {
    width: 200px;
    height: 200px;
    border-top: 10px solid rgba(0, 0, 0, 0.3);
    border-right: 10px solid rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 10%;
    right: 10%;
}

.whyChooseUs .left .imgBorderBottom {
    width: 200px;
    height: 200px;
    border-top: 10px solid rgba(0, 0, 0, 0.3);
    border-right: 10px solid rgba(0, 0, 0, 0.3);
    position: absolute;
    bottom: 6%;
    left: 20%;
    transform: rotate(180deg);
}

.whyChooseUs .right {
    padding-right: 100px;
}

.industries {
    width: 100%;
    height: 100vh;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.industries hr {
    width: 50%;
    margin: 50px 0;
    border-radius: 0 25px 25px 0;
    border: 5px solid rgba(0, 0, 0, 0.3);
}

.industries hr:nth-of-type(2) {
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius: 25px 0 0 25px;
}

.industries .stage {
    width: 85%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin: 50px auto;
}

.industries .stage .item {
    width: 15%;
    text-align: center;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.3);
    padding: 10px 20px;
    border-radius: 50px;
    transition: 0.2s all ease;
    cursor: pointer;
}

.industries .stage .item:hover {
    background: var(--black);
    color: white;
}

.industries .stage .item i {
    font-size: 22px;
}

.services {
    width: 100%;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.services h1 {
    text-transform: uppercase;
    margin: 20px 0 40px 0;
    text-align: center;
    line-height: 50px;
}

.services span {
    color: rgba(0, 0, 0, 0.3);
}

.services .stage {
    width: 95%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px;
    position: relative;
}

.services .card {
    width: 25%;
    padding: 1%;
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 100;
    background: white;
    position: relative;
}

.services .card img {
    width: 100%;
    height: 200px;
}

.services .circle {
    width: 1000px;
    height: 1000px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    position: absolute;
    left: -45%;
    top: 0;
    z-index: 90;
}